<template>
  <div class="home-layout">
    <!-- <el-button type="primary" @click="start" :loading="loading"
      >开始录音</el-button
    >
    <el-button type="primary" @click="stop">结束录音</el-button>
    <el-button type="primary" @click="playVioce">播放录音</el-button> -->
    <div class="h-head">
      <img :src="img.back" />
      <span>智能咨询</span>
    </div>
    <div class="h-body" id="hBody">
      <div
        :class="index % 3 === 2 ? '' : 'right-msg'"
        v-for="(item, index) of list"
        :key="index + 'ds'"
      >
        <img v-if="index % 3 === 2" :src="img.left" class="userImg" />
        <div
          @click="playVioce(item.id, item.sign)"
          :class="index % 3 === 2 ? 'txt1' : 'txt'"
        >
          <span v-if="item && item.sign != 'vioce'"
            >预约挂号、门诊缴费、住院缴费等出 现缴费异常，需用户在医院自助机/
            窗口重新支付，退款失败的费用，医
            院工作人员会统一处理并给予退款</span
          >
          <span v-else><i class="el-icon-video-play"></i>&nbsp;"2</span>
        </div>
        <img v-if="index % 3 !== 2" :src="img.right" class="userImg" />
      </div>
    </div>
    <div class="h-foot">
      <el-button type="primary" @click="start" :loading="loading"
        >开始录音</el-button
      >
      <el-button type="primary" @click="stop">结束录音</el-button>
      <!-- <img :src="img.vioce" />
      <div @click="vioceHandle">
        <el-button :loading="loading">{{
          loading ? "结束录音" : "开始录音"
        }}</el-button>
      </div>
      <img :src="img.send" /> -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      img: {
        back: require("../assets/back.png"),
        left: require("../assets/userLeft.png"),
        right: require("../assets/userRight.png"),
        send: require("../assets/send.png"),
        vioce: require("../assets/vioce.png"),
      },
      loading: false,
      first: true,
      token: "",
      list: [{}, {}, {}, {}, {}],
      id: "",
    };
  },
  methods: {
    vioceHandle() {
      if (!this.first) {
        this.loading = !this.loading;
      }
      if (this.loading) {
        this.stop();
      } else {
        console.log("test");
        this.start();
      }
      if (this.first) this.loading = true;
      this.first = false;
    },
    start() {
      let that = this;
      this.$wx.startRecord({
        success: function (e) {
          // 开始录音的代码处理
          that.loading = true;
          console.log(e, "开始录音");
        },
        cancel: function (e) {
          console.log(e);
        },
      });
    },
    playVioce(id, sign) {
      console.log(id, "播放");
      if (sign === "vioce") this.$wx.playVoice({ localId: id });
    },
    stop() {
      let that = this;
      this.$wx.stopRecord({
        success: function (e) {
          that.loading = false;
          that.list.push({
            sign: "vioce",
            id: e.localId,
          });
          let div = document.getElementById("hBody");
          div.scrollTop = div.scrollHeight;
          that.id = e.localId;
          console.log(e, e.localId, "结束录音");
        },
        cancel: function (e) {
          console.log(e);
        },
      });
    },
  },
  created() {
    // this.getToken();
    console.log("版本5.0");
  },
  mounted() {
    let div = document.getElementById("hBody");
    if (div) {
      div.scrollTop = div.scrollHeight;
    }
  },
};
</script>

<style lang="scss">
@import "./aiHome.scss";
</style>